import {
	goods
} from '@/config/mixins.js'
export default {
	mixins: [goods],
	name: "jshopgoods",
	props: {
		jdata: {
			// type: Array,
			required: true,
		}
	},
	computed: {
		count() {
			return (this.jdata.params.list.length > 0)
		}
	},
	methods: {
		//跳转到商品详情页面
		goodsDetail: function(id) {
			let url = '/pages/goods/index/index?id=' + id;
			this.$common.navigateTo(url);
		},
	},
}
